<template>
<div style="background-color: rgb(247, 247, 247);height:100%;">
    <Header></Header>
    <br>
    <v-card class="qs" max-width="1200">
      <v-card-actions>
        <v-btn style="margin-inline: 1%" @click="showSeq='true'"></v-btn>
        <v-btn style="margin-inline: 1%" @click="showSeq='false'"></v-btn>
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
                v-bind="attrs"
                v-on="on"
                style="margin-inline: 1%" @click="overlay = !overlay"
            >
              
            </v-btn>
          </template>
          <span></span>
        </v-tooltip>
        <v-btn style="background-color:skyblue;color:white;" @click="jumpToCreate"></v-btn>

        <v-overlay :value="overlay">
          <v-card style="margin: 0 auto; width:600px; height: 250px;" class="primary">
            <v-card-title class="text-h6" background-color="lightsteelblue">
              
            </v-card-title>
            <v-card-text style="margin-top:2em; text-align:center;width:500px;" class="primary">
              <v-text-field label="" dense style="width: 30vw; margin-left: 0vw; font-size:20px;"
                            v-model="paperDescription"  color="white"
              ></v-text-field>
            </v-card-text>
            <v-card-actions>
              <v-btn style="margin-inline:1%" color="warning" @click="overlay = false"></v-btn>
              <v-btn color="warning" @click="canceldis"></v-btn>
            </v-card-actions>
          </v-card>
        </v-overlay>

        <v-spacer></v-spacer>
        <div style="font-size:30px;margin-right:50%;font-family:'bgtitlefamily';">  </div>
      </v-card-actions>
    </v-card>
    <br>

    <v-dialog    v-model="tip"  width="500" height="400">
      <v-card>
        <v-card-title class="text-h5 primary lighten-2">
          
        </v-card-title>
        <v-card-text>
            <h3 style="margin-top:1vh;"></h3>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary"  text  @click="deleteConfirm">
            
          </v-btn>
          <v-btn color="primary"  text  @click="deleteCancel">
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>


    <v-dialog v-model="isEdit" width="500" height="400" v-if="isEdit">
      <v-card>
        <v-card-title class="text-h5 primary lighten-2">
          {{editQuestion.type}}
        </v-card-title>
        <v-card-text>
          <h4 style="margin-left:2vw;margin-top:1.5vh;"></h4>
          <v-text-field  style="width:20vw;margin-left:2vw;margin-top:1vh;" dense
            v-model="editQuestion.title" solo :label="editQuestion.title"
        ></v-text-field>
          <h4 style="margin-left:2vw;margin-top:1vh;"></h4>
          <v-radio-group style="margin-left:2vw;" row
            v-model="editQuestion.necessary"
        >
          <v-radio  label="" value="true"></v-radio>
          <v-radio  label="" value="false"></v-radio>
        </v-radio-group>
        <h4 style="margin-left:2vw;margin-top:1.5vh;"></h4>
          <v-text-field  style="width:20vw;margin-left:2vw;margin-top:1vh;" dense
            v-model="editQuestion.description" solo :label="editQuestion.description"
        ></v-text-field>
        <h4 style="margin-left:2vw;margin-top:1vh;" v-if="editQuestion.type==''||editQuestion.type==''"></h4>
          <v-radio-group style="margin-left:2vw;" row
            v-model="editQuestion.ptype" v-if="editQuestion.type==''||editQuestion.type==''"
        >
          <v-radio  label="" value="0"></v-radio>
          <v-radio  label="" value="-1"></v-radio>
        </v-radio-group>
        <div v-if="editQuestion.type==''">
        <h4 style="margin-left:2vw;margin-top:1vh;" ></h4>
        <v-select style="margin-left:2vw;margin-top:1vh;width:10vw;"
          :items="limits"
          v-model="editQuestion.limit"
          label=""
        ></v-select>
        </div>
        <div v-if="editQuestion.type==''">
            <h4 style="margin-left:2vw;margin-top:1vh;" ></h4>
            <v-slider
            v-model="editQuestion.limit"
            color="red darken-4"
            min="1"
            max="10"
        ></v-slider>
          </div>
        <h4 style="margin-left:2vw;margin-top:1vh;" v-if="this.question[this.editIndex].choices[0]!=''"></h4>
        <div v-if="this.question[this.editIndex].choices[0]!=''">
        <div
          v-for="(item, cindex) in editQuestion.choices"
          :key="cindex"
          style="width: 22vw; margin-left: 2vw; "
        >
        <v-row style=" margin-top: 1vh; ">
          <v-text-field
            label="" solo dense
            style="width: 15vw;  margin: 0 0 0 1vw"
            v-model="editQuestion.choices[cindex]"
          ></v-text-field>
          <v-btn
            fab depressed dark
            x-small v-if="cindex==editQuestion.choices.length-1"
            color="success"
            style="
              margin: 1.2vh 0 0 1vw;
              height: 1.2vw;
              width: 1.2vw;
            "
            @click="addChoice(cindex)"
            ><v-icon dark>mdi-plus</v-icon></v-btn
          >
          <v-btn
            fab depressed dark
            x-small
            color="error"
            style="
              margin: 1.2vh 0 0 1vw;
              height: 1.2vw;
              width: 1.2vw;
            "
            @click="deleteChoice(cindex)"
            ><v-icon dark>mdi-close</v-icon></v-btn
          >
          </v-row>
        </div>
        </div>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn color="success" style="margin-left:18vw;" @click="confirmEdit"></v-btn>
          <v-btn color="warning" style="margin-left:2vw;" @click="cancelEdit"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <div style="width: 1200px" class="qs">
      <v-card class="leftpart">
        <v-card-text></v-card-text>
        <LeftBar :titleList="titleList" v-model="choices"></LeftBar>
        <v-card-text></v-card-text>
        <v-text-field v-model="title"  outlined dense style="margin-left:1.5vw;width:12vw;"
        ></v-text-field>
        <v-card-text></v-card-text>
        <v-checkbox
            v-model="isOneTime"
            :label="getIsOneTime"
            style="margin-left:1.5vw;width:12vw;"
        ></v-checkbox>
        <v-card-text></v-card-text>
        <v-menu
            v-model="menu"
            :close-on-content-click="false"
            offset-y
            min-width="auto"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="date"
                append-icon="mdi-calendar"
                readonly
                v-bind="attrs"
                v-on="on"
                outlined
                dense
                background-color="white"
                style="width: 12vw; margin-left: 10%"
              ></v-text-field>
            </template>
            <v-date-picker :allowed-dates="allowedDates"
            v-model="date" @input="menu = false"></v-date-picker>
          </v-menu>
          <v-menu
            ref="menu"
            :close-on-content-click="false"
            :nudge-right="40"
            :return-value.sync="time"
            offset-y
            max-width="290px"
            min-width="290px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="time"
                append-icon="mdi-clock-time-four-outline"
                readonly
                v-bind="attrs"
                v-on="on"
                outlined
                background-color="white"
                dense
                style="width: 12vw; margin-left: 10%"
              ></v-text-field>
            </template>
            <v-time-picker
              v-model="time"
              full-width
              @click:minute="getTime"
            ></v-time-picker>
          </v-menu>

      </v-card>



      <v-card class="rigthpart" style="margin-bottom:20vh;padding-bottom:5vh;">
        <v-card-text style="text-align: center; font-size:25px">
          <v-btn color="success" style="margin-right: 5vw;" @click="issue"></v-btn>
          <v-btn color="primary" style="margin-right: 5vw;" @click="save"></v-btn>
          <v-btn color="warning" style="margin-right: 5vw;" @click="preview"></v-btn>
          <v-btn color="error" style="margin-right: 2vw;" @click="deletePaper"></v-btn>
        </v-card-text>
        <v-card v-for="(item,i) in question" :key="item.id"
        style="margin-left:1vw;margin-right:1vw;margin-top:3vh;" elevation="2">
          <v-row>
          <span style="margin-left:5vw;margin-right:0.5vw;margin-top:2vh;" ><span v-show="getIsShow">{{i+1}}.</span></span>

          <FillBlank v-model="question[i]" v-if="item.type==''" style="margin-top:2vh;"></FillBlank>
          <FillText v-model="question[i]" v-if="item.type==''" style="margin-top:2vh;"></FillText>
          <VoteRadio v-model="question[i]" v-if="item.type==''" style="margin-top:2vh;"></VoteRadio>
          <VoteCheck v-model="question[i]" v-if="item.type==''" style="margin-top:2vh;"></VoteCheck>
          <ScoreBox v-model="question[i]" v-if="item.type===''" style="margin-top:2vh;"></ScoreBox>
          </v-row>

          <v-card-actions style="margin-left:35vw;margin-top:1.5vh;" >
          <v-btn  class="mx-2" fab dark color="cyan" x-small depressed @click="edit(i)">
            <v-icon dark>
              mdi-pencil
            </v-icon>
          </v-btn>
          <v-btn  class="mx-2" fab dark color="red" x-small depressed  @click="close(i)">
            <v-icon dark>
              mdi-close-thick
            </v-icon>
          </v-btn>
          <v-btn  class="mx-2" fab dark color="green" x-small depressed  @click="copy(i)">
            <v-icon dark>
              mdi-file-document-multiple
            </v-icon>
          </v-btn>
          <v-btn  class="mx-2" fab dark color="warning" x-small depressed @click="up(i)">
            <v-icon dark>
              mdi-arrow-up
            </v-icon>
          </v-btn>
          <v-btn  class="mx-2" fab dark color="warning" x-small depressed  @click="down(i)">
            <v-icon dark>
              mdi-arrow-down
            </v-icon>
          </v-btn>
          <v-btn  class="mx-2" fab dark color="warning" x-small depressed  @click="upp(i)">
            <v-icon dark>
              mdi-format-vertical-align-top
            </v-icon>
          </v-btn>
          <v-btn  class="mx-2" fab dark color="warning" x-small depressed  @click="downn(i)">
            <v-icon dark>
              mdi-format-vertical-align-bottom
            </v-icon>
          </v-btn>
          </v-card-actions>
        </v-card>
      </v-card>
    </div>
  </div>
</template>

<script>
import "@/assets/font/font.css"
import QuestionnareCard from '@/components/QuestionnareCard'
import LeftBar from "@/components/Vote/LeftBar";
import Header from "@/components/Header"
import FillBlank from "@/components/FillBlank";
import FillText from "@/components/FillText";
import VoteRadio from "@/components/Vote/VoteRadio";
import VoteCheck from "@/components/Vote/VoteCheck";
import ScoreBox from "@/components/Score/ScoreBox";
var swapItems = function(arr, index1, index2,direction) {
  if(direction=='up'){//
    arr.unshift(arr[index1]);
    arr.splice(index1+1,1);
    return arr;
  }
  if(direction=='down'){//
    arr.push(arr[index1]);
    arr.splice(index1,1);
    return arr;
  }
  arr[index1] = arr.splice(index2, 1, arr[index1])[0];
  return arr;
};
export default {
  name: "SurveyQuestionnare",
  components:{
    QuestionnareCard,
    LeftBar,
    FillBlank,
    VoteRadio,
    VoteCheck,
    Header,ScoreBox,
    FillText,
  },
  computed:{
    getTitle(){
      this.title = this.$route.query.title;
      return this.title;
    },
    getIsOneTime(){
      if(this.isOneTime === 'true' || this.isOneTime === true){
        return "";
      }
      else{
        return ""
      }
    },
    getIsShow(){
      if(this.showSeq === 'false'){
        return false;
      }
      else{
        return true;
      }
    }
  },
  watch:{
    time:function (val, oldVal){
      this.endTime=this.date+' '+this.time+":00"
    },
    date:function (val, oldVal){
      this.endTime=this.date+' '+this.time+":00"
    },
    choices:function (val, oldVal) {
      if(val.indexOf('')!=-1) {
        this.question.push({description:'',ptype:-1,id:this.index,title:'',limit:-1,choices:[''],necessary:true,type:''})
        this.index=this.index+1
      }
      else if(val.indexOf('')!=-1) {
        this.question.push({description:'',ptype:-1,id:this.index,title:'',limit:-1,choices:[''],necessary:true,type:''})
        this.index=this.index+1
      }
      else if(val.indexOf('')!=-1) {
        this.question.push({description:'',ptype:-1,id:this.index,title:'',limit:-1,choices:[''],necessary:true,type:''})
        this.index=this.index+1
      }
      else if(val.indexOf('')!=-1) {
        this.question.push({description:'',ptype:-1,id:this.index,title:'',limit:-1,choices:[''],necessary:true,type:''})
        this.index=this.index+1
      }
      else if(val.indexOf('')!=-1){
        this.question.push({description:'',ptype:-1,id:this.index,title:'',limit:1,choices:['A','B','C','D'],necessary:false,type:''})
        this.index=this.index+1
      }
      else if(val.indexOf('')!=-1){
        this.question.push({description:'',ptype:-1,id:this.index,title:'',limit:4,choices:[''],necessary:false,type:''})
        this.index=this.index+1
      }
      else if(val.indexOf('')!=-1) {
          this.question.push({description:'',ptype:-1,id:this.index,title:'',limit:-1,choices:[''],necessary:false,type:''})
          this.index=this.index+1
        }
      else if(val.indexOf('')!=-1){
        this.question.push({description:'',ptype:-1,id:this.index,title:'',limit:2,choices:['A','B','C','D'],necessary:false,type:''})
        this.index=this.index+1
      }
      else if(val.indexOf('')!=-1){
        this.question.push({description:'',ptype:-1,id:this.index,title:'',limit:-1,choices:[''],necessary:true,type:''})
        this.index=this.index+1
      }
      console.log(this.question);
    }
  },
  mounted(){
    this.id=this.$route.params.id
    this.title=this.$route.query.title
    this.endTime=this.$route.query.endTime

    if(this.$route.query.isonetime!=undefined){
      if(this.$route.query.isonetime === 'true'){
        this.isOneTime = true;
      }
      else{
        this.isOneTime = false;
      }
    }
    if(this.$route.query.date!=undefined&&this.id==-1) this.date = this.$route.query.date;
    if(this.$route.query.time!=undefined&&this.id==-1) this.time = this.$route.query.time;
    if(this.$route.query.paperDescription!=undefined) this.paperDescription=this.$route.query.paperDescription;
    if(this.$route.query.showseq!=undefined) this.showSeq=this.$route.query.showseq
    if(this.$route.query.type == 0)this.question=JSON.parse(this.$route.query.question)
    this.showSeq = this.$route.query.showseq;
    // console.log(this.$route.query.title);
    console.log(this.$route.params.id);
    if(this.id!=-1) this.getData()
  },
  data(){
    return {
      tip:false,
      index:-1000,
      id:-1,
      desdialog:false,
      paperDescription:'',
      title:'',
      date:'',
      menu:'',
      time:'',
      id:"",
      limits:[],
      endTime:'',
      showSeq:true,
      isEdit:false,
      editIndex:-1,
      choices:'',
      overlay: false,
      isOneTime: false,
      editQuestion:{id:this.index,title:'',answer:'',necessary:false,type:''},
      question:[
      ],
      titleList: [{id: 1, title: "", questionList: [{id: 1, question: ""}, {id: 2, question: ""}, {id: 3, question: ""}, {id: 4, question: ""}]},
        {id: 2, title: "", questionList: [{id: 1, question: ""},{id: 2, question: ""} ]},
        {id: 3, title: "", questionList: [{id: 1, question: ""}, ]},
        {id: 4, title: "", questionList: [{id: 1, question: ""},{id: 2, question: ""}]}]
    }
  },
  methods:{
    allowedDates: val => (parseInt(val.split('-')[2], 10)>=new Date().getDate()&&parseInt(val.split('-')[1], 10) ===new Date().getMonth()+1)
        ||parseInt(val.split('-')[0], 10) >new Date().getFullYear()||parseInt(val.split('-')[1], 10) >new Date().getMonth()+1&&val<this.endDate,
    getTime(){
      this.$refs.menu.save(this.time)
      console.log(this.date,this.time);
      return this.time
    },
    close(i){
      this.question.splice(i,1)
      this.index=this.index-1
    },
    copy(i){
      var newques={description:this.question[i].description,id:this.question[i].id,title:this.question[i].title,answer:'',
      limit:this.question[i].limit,necessary:this.question[i].necessary,type:this.question[i].type,choices:[]}
      this.index=this.index+1
      newques.id=this.index
      for(var j=0;j<this.question[i].choices.length;j++) newques.choices.push(this.question[i].choices[j])
      console.log(newques,this.question[i]);
      this.question.splice(i,0,newques)
    },
    up(i){
      if(i==0) {
        this.$message.warning( "" );
        return
      }
      swapItems(this.question, i, i - 1);
    },
    down(i){
      var tmp=this.question[i]
      if(i==this.question.length-1) {
        this.$message.warning( "" );
        return
      }
      swapItems(this.question, i, i + 1);
    },
    upp(i){
      if(i==0) {
        this.$message.warning( "" );
        return
      }
      swapItems(this.question, i, i - 1,'up');
    },
    downn(i){
      var tmp=this.question[i]
      if(i==this.question.length-1) {
        this.$message.warning( "" );
        return
      }
      swapItems(this.question, i, i + 1,'down');
    },
    edit(i){
      this.editIndex=i
      this.editQuestion.title=this.question[i].title
      this.editQuestion.description=this.question[i].description
      this.editQuestion.necessary=String(this.question[i].necessary)
      this.editQuestion.ptype=String(this.question[i].ptype)
      this.editQuestion.choices=this.question[i].choices
      this.editQuestion.copychoices=[]
      this.editQuestion.limit=this.question[i].limit
      for(var k=1;k<=this.question[i].choices.length;k++) this.limits.push(k)
      for(var j=0;j<this.question[i].choices.length;j++) this.editQuestion.copychoices.push(this.question[i].choices[j])
      this.editQuestion.type=this.question[i].type
      console.log(this.editQuestion.choices);
      this.isEdit=true
    },
    cancelEdit(){
      this.question[this.editIndex].choices=this.editQuestion.copychoices
      this.isEdit=false
    },
    confirmEdit(){
      this.isEdit=false
      this.question[this.editIndex].title=this.editQuestion.title
      this.question[this.editIndex].description=this.editQuestion.description
      this.question[this.editIndex].choices=this.editQuestion.choices
      this.question[this.editIndex].limit=this.editQuestion.limit
      this.question[this.editIndex].necessary=eval(this.editQuestion.necessary)
      this.question[this.editIndex].ptype=parseInt(this.editQuestion.ptype)
      console.log(this.question[this.editIndex]);
      this.editIndex=-1
      this.editQuestion={}
    },
    deleteChoice(i){
      this.editQuestion.choices.splice(i,1)
      this.limits=[]
      for(var k=1;k<=this.editQuestion.choices.length;k++) this.limits.push(k)
      console.log(this.editQuestion.choices);
    },
    addChoice(){
      this.editQuestion.choices.push('')
      this.limits=[]
      for(var k=1;k<=this.editQuestion.choices.length;k++) this.limits.push(k)
      console.log(this.editQuestion.choices);
    },
    deletePaper(){
      this.tip=true
    },
    deleteConfirm(){
      this.question=[]
      this.tip=false
    },
    deleteCancel(){
      this.tip=false
    },
    preview(){
      console.log(this.showSeq);
      this.$router.push({path:'/standardpreview',query:{url:'/surveyquestionnare/'+this.id,id:this.id,paperDescription:this.paperDescription,title:this.title,question:JSON.stringify(this.question),showseq:this.showSeq, time: this.time, date:this.date, isonetime:this.isOneTime}})
    },
    a(){
      this.$router.push({path:'/answer',query:{url:'/surveyquestionnare/'+this.id,showSeq:this.showSeq,id:this.id,title:this.title,question:JSON.stringify(this.question)}})
    },
    issue(){
      if(this.question.length==0){
        this.$message.warning('')
        return
      }
      else if(this.endTime==undefined||this.endTime==''){
        this.$message.warning('')
        return
      }
      //for(var i=0;i<this.question.length;i++) this.question[i].choices=JSON.stringify(this.question[i].choices)
      console.log(this.question);
      console.log(this.id);
      for(var i=0;i<this.question.length;i++) this.question[i].numLimit=[]
      this.$axios({url:'/standard/issue',method:'post',data:{
          endTime:this.endTime,
          paperType:'',
          user_id: localStorage.getItem("id"),
          content:this.question,
          status:1,
          paperDescription:this.paperDescription,
          title:this.title,
          id:this.id,
          isSingle: this.isOneTime,
          showSeq:eval(this.showSeq)
        }})
          .then((res) => {
            if(res.data.status==true) {
              this.$message.success("");
              this.$router.push('/manage')
            }
            else this.$message.error({ type: "error", message: res.data.exec });
            console.log(res.data.exec);
          })
          .catch((err) => {
            console.log(err);
            this.$message.error({ type: "error", message: "" });
          });
    },
    save(){
      if(this.question.length==0){
        this.$message.warning('')
        return
      }
      else if(this.endTime==undefined||this.endTime==''){
        this.$message.warning('')
        return
      }
      console.log(this.question);
      console.log(this.$store.state.user_id);
      console.log(this.id);
      console.log(this.endTime);
      console.log(this.paperDescription);
      console.log(this.isOneTime);
      console.log(this.showSeq);
      console.log(localStorage.getItem("id"));
      //for(var i=0;i<this.question.length;i++) this.question[i].choices=JSON.stringify(this.question[i].choices)
      this.$axios({url:'/standard/issue',method:'post',data:{
          endTime:this.endTime,
          paperType:'',
          user_id: localStorage.getItem("id"),
          content:this.question,
          title:this.title,
          paperDescription:this.paperDescription,
          status:0,
          id:this.id,
          isSingle: this.isOneTime,
          showSeq:eval(this.showSeq)
        }})
          .then((res) => {
            if(res.data.status==true) {
              this.$message.success("");
              this.$router.push('/manage')
            }
            else{
              console.log(res.data);
              this.$message.error({ type: "error", message: res.data.exec });
              console.log("")
            }

          })
          .catch((err) => {
            console.log(err);
            this.$message.error({ type: "error", message: "" });
          });
    },
    getData(){
      this.$axios({url:'/standard/content',method:'get',params:{
          id:this.id
        }})
          .then((res) => {
            if(res.data.status==false)  this.$message.error({ type: "error", message: res.data.exec });
            else {
              var array=new Array()
              array=res.data.endTime.split('')
              for(var i=0;i<10;i++)   this.date+=array[i]
              for(var i=11;i<16;i++)   this.time+=array[i]
              console.log(this.date,this.time);
              this.question=res.data.content
              this.title=res.data.title
              this.showSeq=res.data.showSeq
              this.isOneTime=res.data.isSingle
              this.paperDescription=res.data.paperDescription
              console.log(res);
            }
          })
          .catch((err) => {
            console.log(err);
            this.$message.error({ type: "error", message: "" });
          });
    },
    canceldis(){
      this.overlay = false;
      this.paperDescription = "";
    },
    jumpToCreate() {
      this.$router.push('/questionnare');
    }
  }
}
</script>

